export default {
	data() {
		return {
			mobileCode: "获取验证码",
			time: 60
		}
	},
	methods: {
		// 获取验证码
		getMobileCode() {
			this.time = 60
			this.timer()
		},
		timer() {
			if (this.time > 0) {
				this.time--;
				this.mobileCode = "还剩" + this.time + "s"
				setTimeout(this.timer, 1000);
			} else {
				this.time = 0;
				this.mobileCode = "获取验证码";
			}
		}
	}
}
